body {
  background: #F1F1F1; }

#screen-meta-links {
  display: none; }

.geolocation-add-container .button-primary {
  margin-left: 8%;
  margin-top: 5px; }

.conditional-tabs .tab {
  border-bottom: 0;
  cursor: pointer;
  line-height: 20px;
  padding: 15px; }
  .conditional-tabs .tab span[class^="icon-osdxpi-"] {
    color: #3C15B4;
    font-size: 20px;
    margin-right: 3px;
    position: relative;
    top: 4px; }

.deviceinstructions {
  margin-top: 0; }

@media (min-width: 1200px) {
  .devices-container {
    margin-top: -15px; } }

.rule-wrap select:not([multiple]) {
  -moz-appearance: none;
  -webkit-appearance: none;
  background-image: url('data:image/svg+xml;utf8,<?xml version="1.0" encoding="utf-8"?><!DOCTYPE svg PUBLIC "-//W3C//DTD SVG 1.1//EN" "http://www.w3.org/Graphics/SVG/1.1/DTD/svg11.dtd"><svg xmlns="http://www.w3.org/2000/svg" width="14" height="12" version="1"><path d="M4 8L0 4h8z"/></svg>');
  background-position: right 50%;
  background-repeat: no-repeat;
  border-radius: 0;
  height: auto;
  padding-right: 1.5em;
  padding: .3em .4em; }

.rule-wrap input[type="text"] {
  border-radius: 0;
  height: auto;
  padding: .5em; }

.rule-wrap .right-settings {
  border-left: 1px solid #F1F1F1;
  padding-bottom: 45px; }
  @media (min-width: 991px) {
    .rule-wrap .right-settings select,
    .rule-wrap .right-settings input,
    .rule-wrap .right-settings textarea,
    .rule-wrap .right-settings pre.cf-cc-dynamic-link-code {
      max-width: 350px; } }

.left-tabs .condition-label {
