.right-settings .condition-label {
  font-weight: bold;
  padding: 15px 0; }

.conditional-tabs.Device div[data-trigger="Device"], .conditional-tabs.User-Behavior div[data-trigger="User-Behavior"], .conditional-tabs.referrer div[data-trigger="referrer"], .conditional-tabs.url div[data-trigger="url"], .conditional-tabs.Time-Date div[data-trigger="Time-Date"], .conditional-tabs.PageVisit div[data-trigger="PageVisit"], .conditional-tabs.Geolocation div[data-trigger="Geolocation"], .conditional-tabs.custom div[data-trigger="custom"] {
